<script lang="ts">
import { defineComponent, ref, computed, onMounted } from 'vue'
import { gameStorage, type GameRecord, type GameStats } from '@/services/gameStorage'
import { achievementService } from '@/services/achievementService'

export default defineComponent({
  name: 'GameRecordsView',
  setup() {
    const fileInput = ref<HTMLInputElement>()
    const records = ref<GameRecord[]>([])

    const gameTypes = [
      { type: 'addition', name: 'Âä†Ê≥ïÊåëÊàò', icon: '‚ûï' },
      { type: 'subtraction', name: 'ÂáèÊ≥ïÂ§ßÂ∏à', icon: '‚ûñ' },
      { type: 'multiplication', name: '‰πòÊ≥ïË°®', icon: '‚úñÔ∏è' },
      { type: 'division', name: 'Èô§Ê≥ïËææ‰∫∫', icon: '‚ûó' },
      { type: 'fractions', name: 'ÂàÜÊï∞Ê∏∏Êàè', icon: 'üî¢' },
      { type: 'geometry', name: 'Âá†‰ΩïÊé¢Á¥¢', icon: 'üìê' }
    ]

    // Ëé∑ÂèñÊâÄÊúâËÆ∞ÂΩï
    const loadRecords = () => {
      records.value = gameStorage.getAllRecords()
    }

    // Ëé∑ÂèñÊÄª‰ΩìÁªüËÆ°
    const overallStats = computed(() => {
      return gameStorage.getGameStats()
    })

    // Ëé∑ÂèñÊúÄËøëËÆ∞ÂΩïÔºàÊúÄÂ§ö10Êù°Ôºâ
    const recentRecords = computed(() => {
      return records.value
        .sort((a, b) => b.timestamp - a.timestamp)
        .slice(0, 10)
    })

    // Ëé∑ÂèñÁâπÂÆöÊ∏∏ÊàèÁöÑÁªüËÆ°
    const getGameStats = (gameType: string): GameStats => {
      return gameStorage.getGameTypeStats(gameType)
    }

    // Ëé∑ÂèñÊ∏∏ÊàèÂõæÊ†á
    const getGameIcon = (gameType: string): string => {
      const game = gameTypes.find(g => g.type === gameType)
      return game ? game.icon : 'üéÆ'
    }

    // Ëé∑ÂèñÈöæÂ∫¶ÊñáÊú¨
    const getDifficultyText = (difficulty: string): string => {
      const difficultyMap: { [key: string]: string } = {
        easy: 'ÁÆÄÂçï',
        medium: '‰∏≠Á≠â',
        hard: 'Âõ∞Èöæ'
      }
      return difficultyMap[difficulty] || difficulty
    }

    // Ê†ºÂºèÂåñÊó∂Èó¥
    const formatTime = (seconds: number): string => {
      if (seconds < 60) {
        return `${seconds}Áßí`
      } else if (seconds < 3600) {
        const minutes = Math.floor(seconds / 60)
        const remainingSeconds = seconds % 60
        return `${minutes}ÂàÜ${remainingSeconds}Áßí`
      } else {
        const hours = Math.floor(seconds / 3600)
        const minutes = Math.floor((seconds % 3600) / 60)
        return `${hours}Â∞èÊó∂${minutes}ÂàÜ`
      }
    }

    // ÊòæÁ§∫ÁâπÂÆöÊ∏∏ÊàèÁöÑËÆ∞ÂΩï
    const showGameRecords = (gameType: string) => {
      // ËøôÈáåÂèØ‰ª•Ê∑ªÂä†ÂØºËà™Âà∞ËØ¶ÁªÜËÆ∞ÂΩïÈ°µÈù¢ÁöÑÈÄªËæë
      console.log(`Êü•Áúã ${gameType} ÁöÑËØ¶ÁªÜËÆ∞ÂΩï`)
    }

    // Âà†Èô§ËÆ∞ÂΩï
    const deleteRecord = (recordId: string) => {
      if (confirm('Á°ÆÂÆöË¶ÅÂà†Èô§ËøôÊù°ËÆ∞ÂΩïÂêóÔºü')) {
        gameStorage.deleteRecord(recordId)
        loadRecords()
      }
    }

    // ÂØºÂá∫Êï∞ÊçÆ
    const exportData = () => {
      const data = gameStorage.exportGameData()
      if (data) {
        const blob = new Blob([data], { type: 'application/json' })
        const url = URL.createObjectURL(blob)
        const a = document.createElement('a')
        a.href = url
        a.download = `game4math_records_${new Date().toISOString().split('T')[0]}.json`
        document.body.appendChild(a)
        a.click()
        document.body.removeChild(a)
        URL.revokeObjectURL(url)
      }
    }

    // ÂØºÂÖ•Êï∞ÊçÆ
    const importData = () => {
      fileInput.value?.click()
    }

    // Â§ÑÁêÜÊñá‰ª∂ÂØºÂÖ•
    const handleFileImport = (event: Event) => {
      const target = event.target as HTMLInputElement
      const file = target.files?.[0]
      
      if (file) {
        const reader = new FileReader()
        reader.onload = (e) => {
          const content = e.target?.result as string
          if (gameStorage.importGameData(content)) {
            alert('Êï∞ÊçÆÂØºÂÖ•ÊàêÂäüÔºÅ')
            loadRecords()
          } else {
            alert('Êï∞ÊçÆÂØºÂÖ•Â§±Ë¥•ÔºåËØ∑Ê£ÄÊü•Êñá‰ª∂Ê†ºÂºèÔºÅ')
          }
        }
        reader.readAsText(file)
      }
      
      // Ê∏ÖÁ©∫Êñá‰ª∂ËæìÂÖ•
      target.value = ''
    }

    // Ê∏ÖÁ©∫ÊâÄÊúâËÆ∞ÂΩï
    const clearAllRecords = () => {
      if (confirm('Á°ÆÂÆöË¶ÅÊ∏ÖÁ©∫ÊâÄÊúâÊ∏∏ÊàèËÆ∞ÂΩïÂíåÊàêÂ∞±ËøõÂ∫¶ÂêóÔºüÊ≠§Êìç‰Ωú‰∏çÂèØÊÅ¢Â§çÔºÅ')) {
        gameStorage.clearAllRecords()
        achievementService.resetAchievements()
        loadRecords()
        alert('ÊâÄÊúâËÆ∞ÂΩïÂíåÊàêÂ∞±ËøõÂ∫¶Â∑≤Ê∏ÖÁ©∫ÔºÅ')
      }
    }

    onMounted(() => {
      loadRecords()
    })

    return {
      fileInput,
      gameTypes,
      overallStats,
      recentRecords,
      getGameStats,
      getGameIcon,
      getDifficultyText,
      formatTime,
      showGameRecords,
      deleteRecord,
      exportData,
      importData,
      handleFileImport,
      clearAllRecords
    }
  }
})
</script>

